.footer {
    background-color: rgb(0,0, 0, 0.9);
}
.footer__container {
    padding: 32px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer__rights {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-size: 18px;
    font-weight: 400;

    a {
        color: white;
        text-decoration: underline;
    }

    aside {
        color: rgb(255, 255, 255, 0.5);
    }
}