.advantages {
}

.advantages__container {
}

.advantages__cards {
    display: flex;
    gap: 8px;
    justify-content: space-between;
    z-index: 1;
}

.advantages__item {
    min-height: #{fluid(280px, 408px, 1024px, 1360px)};
    flex: 1 1 auto;
    border-radius: 16px;
    padding: 16px;
    position: relative;
    transition: var(--transition);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: white;
    overflow: hidden;
    flex-basis: 20%;
    cursor: pointer;
    z-index: 1;
    
    span, img, p, &::before {
        transition: var(--transition);
    }

    span {
        font-size: 48px;
        font-weight: 500;
        color: var(--orange);
    }

    p {
        font-size: #{fluid(18px, 24px, 1024px, 1360px)};
        font-weight: 500;
        color: black;
        max-width: 190px;
    }

    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        z-index: -2;
    }
    
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(0, 0, 0, 0.4);
        opacity: 0;
        z-index: -1;
    }

    &:hover {
        flex-basis: 33%;

        span, p {
            color: white;
        }
        
        img {
            opacity: 1;
        }

        &::before {
            opacity: 1;
        }
    }
}