*,
*::before,
*::after {
  box-sizing: border-box;
}

p, h1, h2, h3, h4, h5, h6, ul, ol, li {
  padding: 0;
  margin: 0;
}

:focus-visible {
  outline: 2px solid var(--orange);
  outline-offset: -2px;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  line-height: 1.3;
  background-color: #F2EFE6;
  color: rgb(0, 0, 0, 0.9);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.icon {
  fill: transparent;
}

// !-------------------- personal -----------------------

.section {
  padding: 140px 0;
}

.page-title {
  font-size: 48px;
  font-weight: 600;
  margin: 0 auto;
}

.dnone {
  display: none !important;
}

.h0 {
  height: 0 !important;
}

.orange {
  color: var(--orange);
}

.orange-bullet {
  opacity: 1;
}

.op0 {
  opacity: 0;
}

h1 {
  font-size: #{fluid(35px, 60px, 1024px, 1440px)};
  font-weight: 400;
  line-height: #{fluid(50px, 73.38px, 1024px, 1440px)};
}

.btn {
  padding: 24px 48px;
  border-radius: 50px;
  background-color: var(--orange);
  color: white;
  transition: var(--transition);
  border: 1px solid var(--orange);
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;


  svg {
    fill: white;
    transition: var(--transition);
  }

  &:hover {
      background-color: white;
      color: var(--orange);
      border-color: white;

      svg {
          fill: var(--orange);
      }
  }
}

.btn--white {
  color: var(--orange);
  background-color: white;
  border-color: white;
  

  svg {
    fill: var(--orange);
  }

  &:hover {
      background-color: var(--orange);
      color: white;
      border-color: var(--orange);

      svg {
          fill: white;   
      }
  }
}

.btn--transparent {
  color: var(--orange);
  background-color: #E6720526;
  border-color: #E6720526;
  

  svg {
    fill: var(--orange);
  }

  &:hover {
      background-color: var(--orange);
      color: white;
      border-color: var(--orange);

      svg {
          fill: white;   
      }
  }
}

.btn--smaller {
  padding: 14px 32px;
}

.btn--circle {
  width: 53px;
  height: 53px;
  padding: unset;
  aspect-ratio: 1/1;
  display: flex;
  align-items: center;
  justify-content: center;
}


.checkbox-label {
  display: flex;
  gap: 16px;
  align-items: center;
  cursor: pointer;

  input {
      width: 20px;
      height: 20px;
      border:  1px solid rgb(0, 0, 0, 0.3);
      border-radius: 100%;
      appearance: none;
      position: relative;
      cursor: pointer;
      transition: var(--transition);

      &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 100%;
          transform: scale(1);
          background-color: unset;
          transition: var(--transition);
      }

      &:checked {
          border-color: var(--orange);

          &::before {
              transform: scale(0.6);
              background-color: var(--orange);
          }
      }
  }
}

.count-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0 16px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.15);

  label {
    font-size: 18px;
    font-weight: 500;
  }

  &__input {
    display: flex;
    justify-content: center;
    height: 50px;
    background-color: var(--orange);
    border-radius: 100px;
    font-size: 16px;
    overflow: hidden;

    button {
      border: none;
      background: var(--orange);
      font-size: 16px;
      padding: 8px;
      cursor: pointer;
      width: 32px;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        background: color-mix(in srgb, black 5%, var(--orange));
      }
    }
    
    input {
      font-weight: 400;
      width: 60px;      
      border: none;
      background-color: unset;
      text-align: center;
      padding: 0;
      color: white;

      &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
        display: none;
      }
    }
  }
}

.verify-label {
  display: flex;
  gap: 12px;
  align-items: center;

  input {
    appearance: none;
    width: 20px;
    height: 20px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      border-radius: 4px;
      background-color: unset;
      border: 1px solid var(--orange);
      transition: var(--transition);
    }
    
    &::after {
      content: "L";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      border-radius: 4px;
      transform: scale(-1, 1);
      rotate: 45deg;
      color: transparent;
      transition: var(--transition);
    }

    &:checked {
      &::before {
        content: "";
        background-color: var(--orange);
      }
      
      &::after {
        content: "L";
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        margin-top: -1px;
        height: 20px;
        border-radius: 4px;
        transform: scale(-1, 1);
        rotate: 45deg;
        color: white;
      }
    }
  }
}

.common-label {
  display: flex;
  flex-direction: column;
  gap: 8px;

  p {
    font-size: 12px;
    font-weight: 400;
  }

  input {
    font-size: 18px;
    font-weight: 400;
    padding: 16px 12px;
    border-radius: 100px;
    width: 100%;
    background-color: white;
    border: none;
    position: relative;
    overflow: hidden;

    &::placeholder {
      opacity: 0.6;
    }
  }
}