.quiz__container {
    h2 {
        margin: 0 auto 64px;
        max-width: 880px;
        text-align: center;
    }
}

.quiz__swiper {
    position: relative;
    overflow-y: visible;
    overflow-x: hidden;

    .swiper-pagination {
        width: 100%;
        display: flex;
        gap: 2px;
        position: initial;
        max-width: 415px;
    }

    .swiper-pagination-bullet {
        width: unset;
        margin: 0 !important;
        height: 6px;
        border-radius: 3px;
        flex: 1 1 auto;
        background-color: var(--orange);

        &:nth-child(7) {
            display: none;
        }
    }
}

.quiz__pagination-block {
    position: absolute;
    top: 0;
    left: 0;
    width: 415px;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.quiz__slide {
    min-height: 500px;
}

.quiz {
    &__swiper {
        max-width: 500px;
    }

    &__content {
        position: relative;
        width: 100%;
        min-height: fit-content;
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__slide {
        display: flex;
        justify-content: space-between;
        gap: 50px;
        height: auto;

        &:first-child {
            align-items: start;

            // .quiz-info {
            //     height: fit-content;
            // }
        }
        
        .count-label:last-child {
            border-bottom: none;
        }

        &:nth-child(6) .quiz__form, &:nth-child(7) .quiz__form {
            max-width: 500px;
    
            .quiz__next {
                font-size: 17.93px;
            }
        }
    }

    &__form {
        max-width: 450px;
        min-height: 780px;
        height: fit-content;
        padding-top: 100px;
        width: 100%;
        display: flex;
        flex-direction: column;        
        
        h3 {
            font-size: 32px;
            font-weight: 500;
            color: rgb(0, 0, 0, 0.9);
            margin-bottom: 30px;
        }
        
    }

    &__info {
        width: 100%;
        max-width: 815px;
        height: auto;

        &--2 {
            display: none;

            &__item {
                font-size: 24px;
            }
        }
    }

    &__timepicker {
        display: flex;
        flex-direction: column;
        gap: 8px;
        margin-top: 24px;

        span {
            font-size: 12px;
            font-weight: 400;
        }

        input {
            font-size: 18px;
            font-weight: 400;
            padding: 16px 12px;
            border: none;
            border-radius: 100px;

            &::placeholder {
                opacity: 0.6;
            }
        }
    }

}

.quiz-info {
    background-color: white;
    padding: 24px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    height: 100%;

    &--1 {
        height: fit-content;
    }

    &--2 {
        font-size: 24px;
    }

    &--3 {
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            fill: var(--orange)
        }
    }

    h4 {
        font-size: 24px;
        font-weight: 500;
        color: rgb(0, 0,  0, 0.9);
    }

    h5 {
        font-size: 32px;
        font-weight: 600;
        color: rgb(0, 0,  0, 0.9);
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 4px;
        flex-wrap: wrap;

        img {
            width: 20px;
            height: 20px;
        }
    }

    &__price {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: end;

        p {
            font-size: 32px;
            font-weight: 600;
        }
    }
}

.quiz-extra {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 4px;
    list-style: none;
    margin-left: 24px;
    
    &__item {
        font-weight: 400;
        padding-left: 14px;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: calc(50% - 5px);
            left: 0;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            background-color: rgb(0,0, 0, 0.15);
        }
    }
}

.quiz-additional {
    font-size: 16px;
    font-weight: 500;
}

.quiz__map {
    width: 100%;
    aspect-ratio: 812/423;
    border-radius: 16px;
    margin-bottom: 8px;
    background-color: rgb(0, 0, 0, 0.2);
    transition: var(--transition);
}

.quiz-form {
    display: flex;
    flex-direction: column;
    gap: 16px;

    &__btns {
        display: flex;
        align-items: center;
        width: 100%;
        gap: 8px;
        margin-top: auto;

        button {
            position: initial;
            display: flex;
            align-items: center;
            gap: 8px;

            &::before, &::after {
                display: none;
            }
        }
    }
}

.quiz__next {
    svg {
        rotate: 180deg;
    }
}


// ! ------------- datepicker ----------------

.quiz-form__datepicker {
    // display: none;
}
.datepicker__wrapper {
    width: 100%;
    border-radius: 16px;
    color: black !important;
    overflow: hidden;
    font-family: var(--font-family) !important;
    border: none;
    padding: 10px 40px 5px;
}
.datepicker__header {
    background-color: unset;
    font-family: "Lato", sans-serif;
    
    span {
        color: #828282;
        font-size: 20px;
        font-weight: 600;
    }
}
.datepicker__prev, .datepicker__next {
    color: black;
    background-image: none;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        background-color: rgb(0, 0, 0,0.05);
    }
}
.datepicker table {
    border-spacing: 10px;
    border-collapse: unset;
}
.datepicker table th {
    color: black;
    font-size: 12px;
    font-weight: 500;
    font-family: "Lato", sans-serif;
    line-height: 1;
}
.datepicker__next {
    content: "›";
}
.datepicker__daynum {
    font-weight: 500;
    font-size: 20px;
    color: black;
}
.datepicker__day.is-disabled, 
.datepicker__day.is-otherMonth {
    opacity: 0.4;
}
.datepicker__day.is-today div::after {
    background-color: unset;
    color: unset;
    box-shadow: none;
}
// 
.datepicker__day:not(.is-selected) div, 
.datepicker__day:not(.is-selected) div::after, 
.datepicker__day:not(.is-selected) .datepicker__daynum {
    transition: var(--transition);
}
.datepicker__day:not(.is-selected):hover div, 
.datepicker__day:not(.is-selected):hover div::after {
    background: #E672051A;
}
.datepicker__day:not(.is-selected):hover .datepicker__daynum {
    color: var(--orange);
}
// 
.datepicker__day.is-selected div, 
.datepicker__day.is-selected div::after {
    background-color: var(--orange);
    color: white;
}
.datepicker__day.is-selected div, 
.datepicker__day.is-selected div::after, 
.datepicker__day.is-selected .datepicker__daynum {
    transition: var(--transition);
}
.datepicker__day.is-selected:hover div, 
.datepicker__day.is-selected:hover div::after {
    background: var(--orange);
}
.datepicker__day.is-selected:hover .datepicker__daynum {
    color: white;
}
// 
.datepicker__day:hover  {
    color: inherit;
}

// ! =============================================

.quiz-cards {
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 24px;

    h3 {
        font-size: 32px;
        font-weight: 700;
        margin: 0 auto;
    }
    
    &__cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 8px;
    }

    &__verify {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-top: 80px;

        .verify-label {
            width: 100%;
            justify-content: flex-end;
        }
    }
}

.quiz-card {
    flex: 1 1 calc(100%/3 - 16px/3);
    background-color: white;
    padding: 16px;
    height: 270px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;


    svg {
        fill: var(--orange);
    }

    &__texts {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__name {
        font-size: 24px;
        font-weight: 500;
        text-transform: uppercase;
    }

    &__desc {
        font-size: 18px;
        font-weight: 400;
    }
}

.vehicle-img {
    aspect-ratio: 450/250;
    border-radius: 16px;
    overflow: hidden;
    margin-top: 32px;

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.quiz__contacts {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;

    .verify-label {
        margin-top: 24px;
    }
}