.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 24px 0;
    z-index: 10;
    background-color: rgb(0, 0, 0, 0.2);
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__socials {
    display: flex;
    gap: 8px;
}

.header__btns {
    display: flex;
    gap: 16px;
}

