.top-block {
 position: relative;
 z-index: 1;
 margin-top: 0;

 &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, 0.55);
    z-index: -1;
 }
}

.top-block__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 36px;
    color: white;
    min-height: 100vh;
    padding-bottom: 180px;
}

.top-block__advantages {
    display: flex;
    gap: 48px;
}

.top-block__advantage {
    max-width: 190px;
    font-size: 18px;
    font-weight: 500;
    position: relative;

    &::before {
        content: "";
        width: 16px;
        height: 16px;
        position: absolute;
        border-radius: 100%;
        background-color: var(--orange);
        top: -28px;
        left: 0;
    }
}

.top-block__intro {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    h1 {
        max-width: 775px;
        font-size: 56px;
        font-weight: 500;
    }

    button {
        text-transform: uppercase;
        margin-bottom: 8px;
    }
}
