.gallery {
}

.gallery__container {
}

.gallery__swiper {
    border-radius: 16px;
    position: relative;
}

.gallery__swiper-wrapper {
}

.gallery__slide {
    aspect-ratio: 1360/734;
    object-fit: cover;
}

.gallery__swiper-pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2%;
    z-index: 2;

    .swiper-pagination-bullet {
        width: 100%;
        height: 6px;
        border-radius: 10px;
        background-color: white;
    }
}

.gallery__btns {
    position: absolute;
    top: 4%;
    right: 2%;
    display: flex;
    align-items: center;
    gap: 8px;
    height: 64px;
    z-index: 2;
}

.gallery__prev, .gallery__next {
    z-index: 2;
    position: initial;
    background-color: white;
    width: 64px;
    height: 64px;
    border-radius: 100%;
    transition: var(--transition);

    svg {
        width: 18px;
        height: 18px;
        fill: var(--orange);
        transition: var(--transition);
    }

    &::after {
        content: "";
        appearance: none;
    }
    
    &:hover {
        background-color: var(--orange);

        svg {
            fill: white;
        }
    }
}

.gallery__next {
}

.gallery__swiper--upper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0,0, 0, 0.15);
    z-index: 1;
}